// import type effect
import TypeWriter from "../components/home/TypeWriter";

// import icons and images
import {FcExpand} from "react-icons/fc";
import portraitImg from "../assets/images/pages/home/portrait.jpg";
import promiseImg from "../assets/icons/home/clip-1743.png";
import ClientForm from '../components/Client/ClientForm';
import  agileLogo  from '../assets/icons/home/competences/agile-logo.png';
import  apiLogo  from '../assets/icons/home/competences/api-logo.png';
import  axiosLogo  from '../assets/icons/home/competences/axios-logo.png';
import  githubLogo  from '../assets/icons/home/competences/github-logo.png';
import  javascriptLogo  from '../assets/icons/home/competences/javascript-logo.png';
import  mariadbLogo  from '../assets/icons/home/competences/mariadb-logo.png';
import  phpLogo  from '../assets/icons/home/competences/php-logo.png';
import  vueLogo  from '../assets/icons/home/competences/vue-logo.png';
import  wordpressLogo  from '../assets/icons/home/competences/wordpress-logo.png';
import  htmlLogo  from '../assets/icons/home/competences/html-logo.png';
import  cssLogo  from '../assets/icons/home/competences/css-logo.png';
import  mockupSite  from '../assets/images/pages/home/mockup-site.png';
import  reactLogo  from '../assets/icons/home/competences/react-logo.png';
import  oclockLogo  from '../assets/icons/home/oclock-logo.png';

import myCv from '../assets/documents/Christophe Desmarres CV.pdf';

const Home = () => {
    return (
      <div id="home">
        <h1>Bienvenue </h1>
      

        <section id="promise">
        <div className="left">
            <p>Vous souhaitez crer </p>
            <ul>
              <li>un site vitrine</li> 
              <li>un site e-commerce</li> 
              <li>un site de prsentation</li> 
              <li>un site de gestion de contenu</li> 
              <li>une application mtiers</li> 
              <li>ou tout a en mme temps</li> 
            </ul>
            </div>
            <div className="right">
              <img src={promiseImg} alt="promiseImg" className='home__img' />
            </div>
        </section>

        <section id="presentation">
            <h2> propos de moi</h2>
        <div className="left">
            <p>Je suis Christophe Desmarres</p>
            <p>dveloppeur web et web mobile.</p>
            <p>Aprs avoir travaill dans le secteur de l'automobile pendant plus de 18 ans, j'ai dcid de changer de carrire et de me consacrer  ma passion pour l'informatique. Je dveloppe actuellement mes comptences et je suis dtermin  poursuivre mon apprentissage dans ce domaine qui me passionne.</p>
            </div>
            <div className="right">
              <img src={portraitImg} alt="Ankaa" className='portrait' />
            </div>
            <button className="download"><FcExpand className="download__icons"/><a href={myCv} download="CV Christophe Desmarres">Tlcharger mon CV</a></button>
        </section>

        <section id="parcours">
            <h2>Mon parcours en tant que dveloppeur</h2>
        <div className="left">
            <p>Aprs avoir termin une formation intensive en dveloppement web, j'ai travaill sur des projets personnels et professionnels pour dvelopper mes comptences et acqurir de l'exprience. Je suis maintenant en qute de ma premire opportunit en tant que dveloppeur web dans une entreprise qui partage mes valeurs.</p>
            </div>
            <div className="right">
            <TypeWriter options={{
                          strings: ['Dveloppeur web'],
                          autoStart: true,
                          loop: true,
                          delay: 50,
                          deleteSpeed: 50,
                          cursor: '|',
                          cursorClassName: 'cursor',
                          cursorAnimation: 'blink',
                        }}/>
            </div>
        </section>

        <section id="competences">
            <h2>Mes comptences techniques</h2>
        <div className="left">
        <p>Je suis familier avec les mthodologies Agile et Scrum.</p>
        <p>Je travaille avec les technologies suivantes :</p>
            </div>
            <div className="right">
              <img src={htmlLogo} alt="htmlLogo" className="logo" />
              <img src={cssLogo} alt="cssLogo" className="logo" />
              <img src={javascriptLogo} alt="javascriptLogo" className="logo" />
              <img src={phpLogo} alt="phpLogo" className="logo" />
              <img src={vueLogo} alt="vueLogo" className="logo" />
              <img src={reactLogo} alt="reactLogo" className="logo" />
              <img src={wordpressLogo} alt="wordpressLogo" className="logo" />
              <img src={axiosLogo} alt="axiosLogo" className="logo" />
              <img src={apiLogo} alt="apiLogo" className="logo" />
              <img src={mariadbLogo} alt="mariadbLogo" className="logo" />
              <img src={githubLogo} alt="githubLogo" className="logo" />
              <img src={agileLogo} alt="agileLogo" className="logo" />
            </div>
        </section>

        <section id="project">
            <h2>Mes projets en tant que dveloppeur</h2>
        <div className="left">
        <p>L'un de mes projets les plus rcents a t de dvelopper une application web pour une association de parents d'leves de ma commune. J'ai travaill en collaboration avec une quipe de 5 dveloppeurs et avec le client final pour concevoir une interface utilisateur clair et lisible en accord avec la charte graphique de l'cole. J'ai utilis <strong>Vue.js</strong> en frontend et <strong>wordpress</strong> headless pour la partie backend.</p>
            </div>
            <div className="right">
              <img src={mockupSite} alt="mockupSite" className="home__img" />
            </div>
        </section>

        <section id="certifications">
            <h2>Mes certifications en dveloppement</h2>
        <div className="left">
        <p>Titre Professionnel  Dveloppeur Web et Web Mobile inscrit au RNCP 
        <br/>
        (certification de niveau 5 quivalent bac+2 obtenu avec l'cole O'clock)</p>
            </div>
            <div className="right">
              <img src={oclockLogo} alt="oclockLogo" className="home__img" />
            </div>
        </section>

        <section id="contactForm">
        <h2>Me contacter</h2>

          <ClientForm />
        </section>

        </div>

    );
  };
  
  export default Home;